@use 'base/variables' as v;
@use 'base/mixins' as m;

.navegacion{

    display: flex;
    flex-direction: column;
    align-items: center;
    width: 95%;
    gap:1rem;

    @include m.desktop{
        width: auto;
        flex-direction: row;
        gap:2.5rem;
    }

    &__link{
        font-size: 2rem;
        font-weight: 700;
        color: v.$negro;
    
        &--registrar{
            @include m.boton(v.$primario, v.$blanco)
        }
    }
}