.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #e8505b;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 4rem 0;
}
@media (min-width: 1024px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 4rem 0;
  }
}
.header__logo {
  width: 20rem;
  margin-bottom: 5rem;
}
@media (min-width: 1024px) {
  .header__logo {
    margin-bottom: 0;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 95%;
  gap: 1rem;
}
@media (min-width: 1024px) {
  .navegacion {
    width: auto;
    flex-direction: row;
    gap: 2.5rem;
  }
}
.navegacion__link {
  font-size: 2rem;
  font-weight: 700;
  color: #000;
}
.navegacion__link--registrar {
  background-color: #e8505b;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.navegacion__link--registrar:hover {
  cursor: pointer;
  background-color: #e22331;
}
@media (min-width: 768px) {
  .navegacion__link--registrar {
    width: auto;
  }
}

.formulario {
  padding-top: 8rem;
}
.formulario__grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  gap: 8rem;
}
@media (min-width: 1024px) {
  .formulario__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 8rem;
  }
}
.formulario__contenido {
  background-color: #e8505b;
}
.formulario__buscar {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.formulario__buscar h2 {
  font-size: 2.5rem;
  text-align: center;
  color: #FFFFFF;
}
.formulario__buscar input[type=submit] {
  width: 100%;
  height: 4rem;
  border-radius: 10px;
  background-color: #455a64;
  color: #FFFFFF;
  font-weight: 800;
}
@media (min-width: 1024px) {
  .formulario__buscar {
    padding: 5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .formulario__buscar h2 {
    font-size: 3rem;
    text-align: center;
    color: #FFFFFF;
  }
  .formulario__buscar input[type=submit] {
    width: 95%;
    height: 6rem;
    border-radius: 10px;
    background-color: #455a64;
    color: #FFFFFF;
    font-weight: 800;
  }
}
.formulario__campo {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: left;
  padding-bottom: 4rem;
}
.formulario__campo label {
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 1.2rem;
}
.formulario__campo input[type=text] {
  height: 3.5rem;
}
@media (min-width: 1024px) {
  .formulario__campo {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: left;
    padding-bottom: 4rem;
  }
  .formulario__campo label {
    color: #FFFFFF;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 1.2rem;
  }
  .formulario__campo input[type=text] {
    height: 4rem;
  }
}

.pasos {
  margin-top: 5rem;
}
.pasos__contenido {
  background-color: #e8505b;
}
.pasos__heading {
  margin: 0rem;
  text-align: center;
  color: #FFFFFF;
}
.pasos__heading--descripcion {
  font-size: 1.8rem;
  display: block;
}
.pasos__grid {
  background-color: #e8505b;
}
@media (min-width: 768px) {
  .pasos__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.pasos__imagen {
  transition-property: transform;
  transition-duration: 300ms;
}
.pasos__imagen:hover {
  transform: rotate(-4deg) scale(1.1);
}

.testimonial {
  background-color: #455a64;
  padding: 2rem;
  margin-bottom: 2rem;
  border-radius: 2rem;
  color: #FFFFFF;
}
.testimonial:last-of-type {
  margin-bottom: 0;
}
.testimonial__header {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.testimonial__imagen {
  flex-basis: 10rem;
  border-radius: 50%;
  overflow: hidden;
}
.testimonial__autor {
  color: #FFFFFF;
  font-weight: bold;
  margin: 0.5rem 0;
}
.testimonial__text {
  font-size: 2rem;
  line-height: 1.5;
  margin: 4rem 0 0 1rem;
  padding-left: 2rem;
  position: relative;
}
.testimonial__text::before {
  content: '"';
  font-size: 10rem;
  position: absolute;
  left: 0;
  top: -4rem;
  opacity: 0.3;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
}

.testimoniales {
  padding: 5rem 0;
}
.testimoniales__heading {
  text-align: center;
}
@media (min-width: 768px) {
  .testimoniales__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(5, 1fr);
  }
}

@media (min-width: 768px) {
  .testimonial {
    margin-bottom: 0;
  }
  .testimonial:nth-child(1) {
    grid-column: 1/4;
  }
  .testimonial:nth-child(3) {
    grid-column: 5/6;
    grid-row: 1/3;
  }
  .testimonial:nth-child(4) {
    grid-column: 1/3;
  }
  .testimonial:nth-child(5) {
    grid-column: 3/5;
  }
}

.favoritos {
  background-color: rgb(225, 224, 224);
  padding: 5rem 0;
}
.favoritos__heading {
  text-align: center;
  color: #455a64;
}
@media (min-width: 768px) {
  .favoritos__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.favorito {
  display: grid;
  grid-template-rows: repeat(5, 1fr);
  align-items: center;
  gap: 2rem;
  margin-bottom: 4rem;
}
@media (min-width: 768px) {
  .favorito {
    display: grid;
    grid-template-rows: repeat(5, 1fr);
    gap: 4rem;
  }
}
.favorito__grid {
  background-color: white;
  box-shadow: 0px 0px 2.6rem -0.8rem #969292;
  padding: 1.8rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .favorito__grid {
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 2rem;
    align-items: center;
  }
}
.favorito__imagen {
  max-width: 35%;
  margin: 0 auto;
  padding-bottom: 1rem;
}
@media (min-width: 768px) {
  .favorito__imagen {
    max-width: 100%;
    margin: 0 auto;
  }
}
.favorito__contenido {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  row-gap: 3rem;
}
.favorito__contenido img {
  width: 30%;
}
@media (min-width: 768px) {
  .favorito__contenido img {
    width: 60%;
  }
}
.favorito__nombre {
  margin: 0;
}
.favorito__descripcion {
  text-align: justify;
  margin: 0;
}

@media (min-width: 768px) {
  .gana {
    display: grid;
    grid-template-columns: 1fr 1.5fr;
    gap: 5rem;
  }
}
.gana__contenido {
  padding-top: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  row-gap: 2rem;
}
.gana__descripcion {
  text-align: justify;
}
.gana__boton {
  margin: 0 auto;
}
.gana__boton button {
  background-color: #e8505b;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.gana__boton button:hover {
  cursor: pointer;
  background-color: #e22331;
}
@media (min-width: 768px) {
  .gana__boton button {
    width: auto;
  }
}

.ganaDinero {
  padding-top: 5rem;
  padding-bottom: 5rem;
}
.ganaDinero__heading {
  text-align: center;
}

.descargar {
  background-color: #e1e0e0;
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .descargar__grid {
    display: grid;
    grid-template-columns: 1fr 2fr;
    align-items: center;
  }
}
@media (min-width: 768px) {
  .descargar__imagen {
    width: 60%;
    margin: 0 auto;
    transition: transform 0.5s ease-in-out;
    transition: width 0.5s ease-in-out, height 0.5s ease-in-out;
  }
  .descargar__imagen:hover {
    transform: rotate(10deg);
    width: 65%;
  }
}

.tiendas {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
.tiendas__grid {
  width: 100%;
  display: grid;
  grid-template-rows: 1fr;
  justify-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .tiendas__grid {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
  }
}
.tiendas__enlace {
  width: 90%;
}
@media (min-width: 768px) {
  .tiendas__enlace {
    width: 70%;
    transition: width 0.5s ease-in-out, height 0.5s ease-in-out;
  }
  .tiendas__enlace:hover {
    width: 90%;
    height: 90%;
  }
}
.tiendas__heading, .tiendas__texto {
  margin: 0 auto;
}
.tiendas__heading {
  font-weight: bold;
  color: #455a64;
}
.tiendas__texto {
  font-size: 1.5rem;
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #455a64;
  padding-top: 5rem;
}
.footer__copyringht {
  font-size: 1.2rem;
  background-color: #e8505b;
  margin: 0 auto;
  width: 100%;
  text-align: center;
  padding: 2rem 0 2rem 0;
}
.footer__grid {
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  row-gap: 5rem;
  padding-bottom: 3rem;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 5rem;
  }
}
.footer__heading {
  color: #FFFFFF;
  margin: 0;
  padding-bottom: 1rem;
}
.footer__texto {
  color: #FFFFFF;
  text-align: justify;
  margin: 0;
}
.footer__nav {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.footer__link {
  text-decoration: none;
  color: #FFFFFF;
}